body {
  margin: 0;
}
#header_wrapper {
  position: relative;
  width: 100%;
  background: #005825;
}
#header {
  height: 100px;
  margin: 0 auto;
}
#welcome {
  position: relative;
  float: right;
  top: 40%;
  color: white;
}
#logo > img {
  position: absolute;
  top: 50%;
  margin-top: -42.5px;
}
.login {
  margin-top: 150px;
}
.login .alert {
  margin-top: .5rem !important;
}
.login-signup {
  float: right;
  margin-top: 20px;
  margin-right: 25%;
}
.logout {
  float: right;
}
.home-content {
  margin: 50px auto;
  text-align: center;
}
.home-content-sign {
  font-size: 50px;
}
.table-btn {
  height: 1.4em;
  margin-left: 1px;
  padding-left: .5em !important;
  padding-right: .5em !important;
}
.table-btn span {
  position: relative;
  top: -0.4em;
}
.search {
  width: 400px;
  float: right;
}
.btn-group {
  display: inline-block;
  float: right;
}
.excel {
  position: relative;
}
.excel .form-group {
  margin: .4em 0;
}
.excel-submit {
  position: relative;
  margin: 100px auto;
}
.excel .file {
  width: 50%;
}
.excel .file-alert {
  margin-top: 25%;
}
.signup-form {
  margin-top: 50px;
  padding-left: 250px;
}
.signup-form h3 {
  margin-bottom: 20px;
  margin-left: -20px;
}
.signup-form-btn {
  margin-left: 600px;
}
.stu-excp-content {
  position: relative;
  margin: 0 auto;
  text-align: auto;
}
.stu-excp-content__unit {
  margin-top: 10px;
}
.stu-excp__submit {
  position: absolute;
  top: 0px;
  right: 10px;
}
.stu-excp__ta-statu {
  color: red;
}
.stu-excp__ta-statu + span {
  color: green;
}
